@font-face {
  font-family: "Henriette Bold";
  font-style: normal;
  font-weight: 400;
  src: url("../Commons/Henriette\ Bold.otf"); /* IE9 Compat Modes */
  src: local("Open Sans"), local("OpenSans"),
    url("../Commons/Henriette\ Bold.otf") format("embedded-opentype"),
    /* IE6-IE8 */ url("../Commons/Henriette\ Bold.otf") format("woff2"),
    /* Super Modern Browsers */ url("../Commons/Henriette\ Bold.otf")
      format("woff"),
    /* Modern Browsers */ url("../Commons/Henriette\ Bold.otf")
      format("truetype"),
    /* Safari, Android, iOS */ url("../Commons/Henriette\ Bold.otf")
      format("svg"); /* Legacy iOS */
}
body {
  font-family: "Henriette Bold";
}

.gift-header1 {
  top: 27.5vh;
  left: 8.3vw;
  width: 36vw;
  /* height: 166px; */
  font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-bold)
    var(--unnamed-font-size-48) / var(--unnamed-line-spacing-56)
    var(--unnamed-font-family-henriette);
  letter-spacing: var(--unnamed-character-spacing-0);
  color: var(--unnamed-color-000000);
  text-align: left;
  font: normal normal bold 48px/56px Henriette;
  letter-spacing: 0px;
  color: #000000;
  opacity: 1;
}

.gift-header3 {
  margin-top: 3.8vh;
  left: 8.3vw;
  width: 410px;
  /* height: 62px; */
  font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-bold)
    var(--unnamed-font-size-24) / var(--unnamed-line-spacing-32)
    var(--unnamed-font-family-henriette);
  letter-spacing: var(--unnamed-character-spacing-0);
  color: var(--unnamed-color-000000);
  text-align: left;
  font: normal normal bold 24px/32px Henriette;
  letter-spacing: 0px;
  color: #000000;
  opacity: 1;
}

.gift-paragraph {
  margin-top: 19vh;
  left: 8.3vw;
  width: 28vw;
  /* height: 29px; */
  font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-300)
    var(--unnamed-font-size-24) / var(--unnamed-line-spacing-32)
    var(--unnamed-font-family-basic-sans);
  letter-spacing: var(--unnamed-character-spacing-0);
  color: var(--unnamed-color-000000);
  text-align: left;
  font: normal normal 300 24px/32px Basic Sans;
  letter-spacing: 0px;
  color: #000000;
  opacity: 1;
}

.gift-button {
  background: #ffffff 0% 0% no-repeat padding-box;
  border: 2px solid #545871;
  border-radius: 8px;
  opacity: 1;
  color: #545871;

  cursor: pointer;
  /* text-decoration: underline; */
  /* display: inline; */
  font-size: 24px;
  font-family: Sans-Serif, "Henriette Bold";

  padding-top: 18px;
  padding-right: 32px;
  padding-bottom: 18px;
  padding-left: 32px;

  margin-top: 17px;
  /* margin-bottom: 100px; */
  margin-right: 16px;
  /* margin-left: 80px; */
}

.gift-button:hover,
.gift-button:focus {
  /* text-decoration: none;
  outline: none;
  box-shadow: none; */
  color: whitesmoke;
  background-color: #545871;
}

.gift-button-inverse {
  background-color: #545871;
  color: white;
  font-weight: lighter;
  border: none;
  cursor: pointer;
  /* text-decoration: underline; */
  /* display: inline; */
  font-size: 24px;
  font-family: Sans-Serif, "Henriette Bold";

  padding-top: 18px;
  padding-right: 32px;
  padding-bottom: 18px;
  padding-left: 32px;

  margin-top: 54px;
  margin-bottom: 100px;
  margin-right: 16px;
  /* margin-left: 80px; */
}

.gift-button-inverse:hover,
.gift-button-inverse:focus {
  text-decoration: none;
  outline: none;
  box-shadow: none;
}

.product-card {
  background-color: #545871;
  color: white;
  font-weight: lighter;
  border: none;
  border-radius: 9px;
  cursor: pointer;
  padding-right: 32px;
  padding-left: 32px;
  margin: 10px;
  width: calc(100vw / 7);
  /* text-decoration: underline; */
  /* display: inline; */
  /* font-size: 24px;
  font-family: Sans-Serif, "Henriette Bold";

  padding-top: 18px;
  padding-right: 32px;
